import '../App.css';
import Canvas from './Canvas';
import Header from './Header';
import React from 'react'
import Confetti from 'react-confetti'
import { render } from '@testing-library/react';
import {useState} from 'react';

export default function Bg({a}){
    //오전+오후
    let [hours,setHours] = useState();
    function headProps(st){
        setHours(st);
    }

    a(hours);
    

    //폭죽 라이브러리 사용
    function pop(){
        render(
            <Confetti
                width={window.innerWidth}
                height={window.innerHeight}
                recycle={false}
            />
        )
    }

    function star() {
        if(hours == 'night'){
    
            for(let i=0; i<100; i++){
                var ele = document.createElement('div');
        
                // document.querySelector('canvas').style.width = window.innerWidth;
                // document.querySelector('canvas').style.height = window.innerHeight;
        
                ele.classList.add('little-star');
                ele.style.top = `${Math.random() * 400}px`;
                ele.style.left = `${Math.random() * 10000}px`;
        
                // document.querySelector('body').style.width = `${document.body.clientWidth}px`;
                // document.querySelector('body').style.height = `${document.body.clientHeight}px`;
        
                document.querySelector('.star').appendChild(ele);
            }
        }
    }

    star()

    function toggle() {
        var toggle = document.querySelector('input');

        if(toggle.checked == true){
            // console.log('true'); 낮

        }else{
            // console.log('false') 밤

        }
    }

    return(
        <div>
            <Header status={hours}/>
            <div className="star"></div>
            <Canvas d={headProps}/>
            <div className={hours == "day" ? 'balloon' : 'moon'} onClick={pop}></div>
        
            {/* <input type="checkbox" id="c" onClick={toggle}/>
            <label for="c">
                <div id="a">
                    <div id="c1"></div>
                    <div id="c2"></div>
                    <div id="c3"></div>
                </div>
            </label> */}
        </div>
    )
}