export class Hill{
    constructor(color,speed,total){
        this.color = color;
        this.speed = speed;
        this.total = total;
    }

    resize(stageWidth, stangeHeight){
        this.stageWidth = stageWidth;
        this.stangeHeight = stangeHeight;

        this.points = [];
        this.gap = Math.ceil(this.stageWidth / (this.total - 2));

        for(let i = 0; i<this.total; i++){
            this.points[i] = {
                x : i * this.gap,
                y : this.getY()
            };
        }
    }

    draw(ctx){
        ctx.fillStyle = this.color;
        ctx.beginPath();

        let cur = this.points[0];
        let prev = cur;

        let dots = [];
        cur.x += this.speed;

        if(cur.x > -this.gap){
            this.points.unshift({
                x: -(this.gap * 2),
                y: this.getY()
            });
        }else if(cur.x > this.stageWidth + this.gap){
            this.points.splice(-1);
        }
        //화면이 일정 영역이상에서 사라지면 배열에서 빼줘서 언덕이 끊임없이 그려짐

        ctx.moveTo(cur.x, cur.y);

        let prevCx = cur.x;
        let prevCy = cur.y;

        for(let i=1; i<this.points.length; i++){
            cur = this.points[i];
            cur.x += this.speed;
            const cx = (prev.x + cur.x) / 2;
            const cy = (prev.y + cur.y) / 2;

            ctx.quadraticCurveTo(prev.x, prev.y, cx, cy); //곡선을 그림

            dots.push({ //그린 곡선의 값을 배열에 추가
                x1: prevCx,
                y1: prevCy,
                x2: prev.x,
                y2: prev.y,
                x3: cx,
                y3: cy,
            });

            prev = cur;
            prevCx = cx;
            prevCy = cy;
        }

        ctx.lineTo(prev.x, prev.y);
        ctx.lineTo(this.stageWidth, this.stangeHeight);
        ctx.lineTo(this.points[0].x, this.stangeHeight);
        ctx.fill();

        return dots;
    }

    getY(){
        const min = this.stangeHeight / 3;
        const max = this.stangeHeight - min;

        return min+Math.random()*max;
    }
    //언덕의 Y값을 랜덤하게 주기 위해서
}