import React, {useEffect, useState} from 'react';
import { Link } from 'react-router-dom';
import '../App.css';
import DataList from '../data/workList.json';

export default function Detail(match){
    // window.location.replace("/detail");
    const [isOpen, setMenu] = useState(false);
    const toggleMenu = function(){
        setMenu(isOpen => !isOpen);
        const hmenu = document.querySelector('.hmenu');
        hmenu.classList.toggle('open');
    }

    function handleScroll(){
        if(ScrollY >= 10){
            setScrollY(window.scrollY);
            setScrollActive(true);
        }else{
            setScrollY(window.scrollY);
            setScrollActive(false);
        }
    }

    useEffect(()=>{
        function scrollListener(){
            window.addEventListener("scroll",handleScroll);
        }
        scrollListener();
        return()=>{
            window.removeEventListener("scroll",handleScroll);
        };
    });

    const [ScrollY, setScrollY] = useState(0);
    const [ScrollActive, setScrollActive] = useState(false);
    
    const id = match.location.pathname.split(':');
    const num = id[1];
    const list = DataList.List

    const images = [];

    DataList.List[num].img.map((img,key)=>(
        images.push({url : img})
    ))

    const next = Number(num)+1;

    const white = {background : '#fff'}

    function nextPage(){
        if(next == list.length){
            window.location.href=`:0`;
        }else{
            window.location.href=`:${next}`;
        }
    }

    const width = ["1920","1536","640"]
    const height = ["1080","2048","1136"]

    function resize(url,width,height){
        window.open(url,"url",`width=${width}, height=${height} history=no, resizable=no, status=no, scrollbars=no, menubar=no`)
    }

    const responsive = DataList.List[num].respon.map((item,key)=>(
        <p className="about-resume" onClick={()=>{resize(DataList.List[num].url, width[key], height[key])}} id={key}>{item}</p>
    ));

    return(
        <>
        <div className="header">
            <div onClick={toggleMenu} className={isOpen ? "hmenu open" : "hmenu"}>
                <span style={white}></span>
                <span style={white}></span>
                <span style={white}></span>
            </div>
        </div>

        <div className={isOpen ? "show-menu" : "hide-menu"}>
            <ul>
                <li><Link to="/">Main</Link></li>
                <li><Link to="/Work">Works</Link></li>
                <li><Link to="/About">About</Link></li>
            </ul>
        </div>

        <div className={ScrollActive ? "detail-bg-scroll" : "detail-bg"} style={{background:"url("+list[num].bg+") no-repeat center center", backgroundSize:"cover"}}>
            <div className="detail-decs">
                {list[num].type1}
            </div>
            <div className="detail-tit">
                {list[num].tit}
            </div>
        </div>

        <div className={ScrollActive ? "hide-scroll" : "scroll"}>
                <div className="fix w"></div>
                <div className="ani w"></div>
        </div>

        <div className="detail-sub">
            <div className="detail-sub-tit">{list[num].tit_kor}</div>
            {/* <div className="detail-inner"> */}

                <div className="about-desc">
                    <p className="desc-tit">INFO</p>

                    <div className="about-sub-desc">
                        <p className="tit-inner">
                            <span>개발기간</span>
                            {list[num].date}
                        </p>

                        <p className="tit-inner">
                            <span>설명</span>
                            {list[num].desc}
                        </p>

                        <p className="tit-inner">
                            <span>개발언어</span>
                            <div className="box-tec">
                                <ul>
                                    {
                                        DataList.List[num].skill.map((skill,key)=>(
                                            <li key={key}>{skill}</li>
                                        ))
                                    }
                                </ul>
                            </div>
                        </p>

                        <p className="tit-inner">
                            <span>담당업무</span>
                            <ul>
                                {
                                    DataList.List[num].job.map((job,key)=>(
                                        <li key={key}>{job}</li>
                                    ))
                                }
                            </ul>
                        </p>
                    </div>
                </div> 

                <div className="about-desc">
                    <p className="desc-tit">LINK</p>

                    <div className="about-sub-desc">
                        <div style={{display : "flex"}}>
                            <p className="about-resume">기획서</p>
                            {responsive}
                        </div>
                    </div>
                </div>  

                <div className="about-desc">
                    <p className="desc-tit">VIEW</p>

                    <div className="about-sub-desc">
                        <iframe title="Portfolio site" src={list[num].url} class="portfolio-view"></iframe>
                    </div>
                </div>           
            {/* </div> */}


            {/* <div className="detail-pic">
                <SimpleImageSlider
                    width={1280}
                    height={780}
                    images={images}
                    showBullets={false}
                    showNavs={true}
                    navStyle={2}
                >
                </SimpleImageSlider>
            </div> */}
        </div>

        <div className="next" onClick={nextPage}>
            <div className="next-fade">
                {next == list.length ? list[0].tit : list[next].tit}
            </div>
            <div className="next-img">
                <img src={next == list.length ? list[0].bg : list[next].bg}></img>
            </div>
        </div>
        </>

    )
}