import {Hill} from './Hill';

export default class Bgcanvas {
    
    constructor(canvasElement){
        this.canvas = canvasElement
        this.ctx = canvasElement.getContext('2d');

        // document.body.appendChild(this.canvas);
        // 캔버스 추가해서 body에 추가
        this.toMeridium = new Date();
        this.hours = this.toMeridium.getHours() < 12 ? "day":"night";
        (this.hours == 'day') ? this.check(true) : this.check(false);
    }

    check(checkToggle){
       
        if(this.hours == 'day' && checkToggle){
            this.hills = [
                new Hill('#333311', 0.2, 12),
                new Hill('#6D7441', 0.5, 8),
                new Hill('#97A26A', 0.8, 6),
                new Hill('#B5C855', 1.1, 4),
                new Hill('#C6CEA7', 1.3, 2),
            ];
        }else{
            this.hills = [
                new Hill('#301F43', 0.2, 12),
                new Hill('#5E278D', 0.5, 8),
                new Hill('#5E339E', 0.8, 6),
                new Hill('#6837A4', 1.1, 4),
                new Hill('#CE9DEF', 1.3, 2),
            ];
        }

        window.addEventListener('resize',this.resize.bind(this),false); //?
        this.resize();
        //스크린 사이즈를 가져오기 위해서 resize이벤트를 걸어줌

        requestAnimationFrame(this.animate.bind(this));
    }

    resize(){
        this.stageWidth = document.body.clientWidth; //?
        this.stageHeight = document.body.clientHeight;

        this.canvas.width = this.stageWidth * 2;
        this.canvas.height = this.stageHeight * 2;
        this.ctx.scale(2,2); 
        //캔버스 사이즈를 키워서 레티나 디스플레이에도 적용

        for(let i=0; i<this.hills.length; i++){
            this.hills[i].resize(this.stageWidth, this.stageHeight);
        }
    }

    animate(t){
        requestAnimationFrame(this.animate.bind(this));

        this.ctx.clearRect(0,0,this.stageWidth,this.stageHeight);
        //캔버스를 지워주는 코드

        let dots;
        for (let i=0; i<this.hills.length; i++){
            dots = this.hills[i].draw(this.ctx);
        }
    }
}