import React, {useEffect, useState} from 'react';
import '../App.css';
import Bg from '../comp/Bg';

export default function Main(){

    //오전+오후
    let [hours,setHours] = useState();
    function headProps(st){
        setHours(st);
    }

    const [mobileSize, setMobileSize] = useState(false);
    
    const mobileResize = ()=>{

        if(window.innerWidth < 850){
            setMobileSize(true);
        }else{
            setMobileSize(false);
        }
    }

    useEffect(()=>{
        window.addEventListener('load',mobileResize);
        window.addEventListener('resize',mobileResize);
        return()=>{
            window.removeEventListener('resize',mobileResize);
        }
    },[]);

    return(
        <div>
        <Bg a={headProps} style={{overflow:"hidden"}}/>

        <div className="clickMe">Click Me!</div>
        <div className={mobileSize ? 'title res' : 'title'}>
            <span className="dotTitle">Hello</span><br/>
            I am<br/>
            Developer
        </div>

        <div className={hours == 'day' ? 'copyright':'copyright-w'}>
            Copyright© 2021 JINKAYEONG
        </div>
        </div>
    )
}