import '../App.css';
import { Link } from 'react-router-dom';
import React, {useEffect, useState} from 'react';

export default function Header({status}){
    
    const [isOpen, setMenu] = useState(false);
    const toggleMenu = function(){
        setMenu(isOpen => !isOpen);
        const hmenu = document.querySelector('.hmenu');
        hmenu.classList.toggle('open');
    }

    let [color,setColor] = useState(status);
    useEffect(()=>{
        setColor(status);
    },[status])

    return(
        <div>
            <div className="header">
                {/* <div className="emoji" id={color}></div> */}
                <div onClick={toggleMenu} className={isOpen ? "hmenu open" : "hmenu"}>
                    <span id={color}></span>
                    <span id={color}></span>
                    <span id={color}></span>
                </div>
            </div>

            <div className={isOpen ? "show-menu" : "hide-menu"}>
                <ul>
                    <li><Link to="/">Main</Link></li>
                    <li><Link to="/Work">Works</Link></li>
                    <li><Link to="/About">About</Link></li>
                </ul>
            </div>
        </div>
    )
}