import React, { useRef, useState, useEffect } from 'react'
import Bgcanvas from './Bgcanvas';
import canvas from './Bgcanvas'

export default function Canvas({d}) {

  var toMeridium = new Date();
  var hours = toMeridium.getHours() < 20 ? "day":"night";
  d(hours);

  const myCanvas = useRef();
  const myCheck = useRef();

  const [canvasObj, setCanvasObj] = useState();
  const [checkToggle, setCheckToggle] = useState(false);

  useEffect( () => {

      setCanvasObj( new canvas(myCanvas.current, checkToggle) );
      
  } ,[myCanvas])

  function toggle() {
    setCheckToggle(!checkToggle);
    canvasObj.check(checkToggle);
  }

return (
    <div>
      <input type="checkbox" id="c" onClick={toggle} ref={myCheck}/>
      <label for="c">
          <div id="a">
              <div id="c1"></div>
              <div id="c2"></div>
              <div id="c3"></div>
          </div>
      </label>

      <canvas className={hours == 'day' ? "day_bg" : "night_bg"} ref={myCanvas} onLoad={()=>{canvasObj.animate();}}>
      </canvas>
    </div>
  );
}